@import "../utils/utils.less";

.about {
  position: relative;
  z-index: 9;
  min-width: 100%;
  width: 100%;
  background-color: #192144;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;

  @media (min-width: @tablet-width) {
    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 60px;
    }

    @media (min-width: @desktop-width) {
      border-bottom-left-radius: 60px;
      border-bottom-right-radius: 60px;
      }
}

.about__container {
  display: flex;
  flex-direction: column;
  min-width: 318px;
  width: 318px;
  min-height: 341px;
  margin-top: 0px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 16px;
  background-image: url(../img/about-mobile@1x.png);
  color: #FFFFFF;
  border: solid 1px transparent;

  @media (min-resolution: 2dppx) {
    background-image: url(../img/about-mobile@2x.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 292px 353px;
  }

  @media (min-width: @tablet-width) {
    background-image: url(../img/about-tablet@1x.png);
    background-repeat: no-repeat;
    background-position: 382px 139px;
    min-width: 766px;
    width: 766px;
    min-height: 759px;
    background-color: #182044;
    margin-top: 0px;
    margin-left: auto;
    margin-right: auto;

    @media (min-resolution: 2dppx) {
      background-image: url(../img/about-tablet@2x.png);
      background-repeat: no-repeat;
      background-position: 382px 139px;
      background-size: 376px 548px;
    }
  }

  @media (min-width: @desktop-width) {
    margin-top: -3px;
    margin-left: auto;
    margin-right: auto;
    background-image: url(../img/about-desktop@1x.png);
    background-repeat: no-repeat;
    background-position: 750px 82px;
    min-width: 1438px;
    width: 1438px;
    min-height: 731px;
    background-color: #182044;
    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 60px;
    position: relative;
    z-index: 9;

    @media (min-resolution: 2dppx) {
      background-image: url(../img/about-desktop@2x.png);
      background-repeat: no-repeat;
      background-position: 750px 82px;
      background-size: 630px 665px;
    }
  }
}

.about__title {
  color: #FF8D30;
  font-family: "Blogger Sans", Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  padding-left: 48px;

  @media (min-width: @tablet-width) {
    font-family: "Blogger Sans", Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 45px;
    line-height: 45px;
    color: #FF8D30;
    margin-top: 113px;
    margin-bottom: 0px;
    margin-left: 23px;
    margin-right: 0px;
  }

  @media (min-width: @desktop-width) {
    font-style: normal;
    font-weight: bold;
    font-size: 45px;
    line-height: 45px;
    margin: 110px 0px 0px 82px;
  }
}

.about__quote {
  display: inline-block;
  width: 211px;
  font-family: "Blogger Sans", Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 22px;
  margin-left: 2px;
  margin-top: 0px;
  padding-left: 46px;

  @media (min-width: @tablet-width) {
    display: inline-block;
    width: 300px;
    font-family: "Blogger Sans", Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 28px;
    line-height: 33px;
    color: #FFFFFF;
    margin-top: 42px;
    margin-bottom: 0px;
    margin-left: 26px;
    margin-right: 0px;
  }

  @media (min-width: @desktop-width) {
    display: inline-block;
    width: 619px;
    height: 102px;
    font-style: normal;
    font-weight: 300;
    font-size: 28px;
    line-height: 34px;
    margin: 59px 0px 0px 84px;
  }
}

.about__slogan {
  display: inline-block;
  width: 202px;
  font-family: "Blogger Sans", Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
  margin: 0px;
  padding-top: 3px;
  padding-left: 48px;

  @media (min-width: @tablet-width) {
    display: inline-block;
    width: 260px;
    font-family: "Blogger Sans", Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 21px;
    line-height: 25px;
    color: #FFFFFF;
    margin-top: 28px;
    margin-bottom: 0px;
    margin-left: 23px;
    margin-right: 0px;
  }

  @media (min-width: @desktop-width) {
    width: 425px;
    height: 48px;
    font-style: normal;
    font-weight: bold;
    font-size: 21px;
    line-height: 24px;
    padding: 0px 0px 0px 0px;
    margin: 38px 0px 0px 131px;
  }
}

.about__btn {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
    width: 276px;
    height: 80px;
    background-color: #FFD74B;
    background-image: url(../img/icon-btn-triangle.svg);
    background-repeat: no-repeat;
    background-position: 87%, 50%;
    border-radius: 20px;
    padding: 0px 0px 0px 0px;
    margin: 54px 0px 0px 71px;
  }

  @media (min-width: @desktop-width) {
    display: block;
    width: 274px;
    height: 80px;
    background-color: #FFD74B;
    background-image: url(../img/icon-btn-triangle.svg);
    background-repeat: no-repeat;
    background-position: 87%, 50%;
    border-radius: 20px;
    padding: 0px 0px 0px 0px;
    margin: 87px 0px 0px 131px;
  }
}

.about__btn-text {
  display: none;

  @media (min-width: @tablet-width) {
    display: inline-block;
    font-family: "Blogger Sans", Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 78px;
    color: #1D2E5B;
    padding: 0px 0px 0px 0px;
    margin: 0px 0px 0px 33px;
  }

  @media (min-width: @desktop-width) {
    display: inline-block;
    font-family: "Blogger Sans", Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 78px;
    color: #1D2E5B;
    padding: 0px 0px 0px 0px;
    margin: 0px 0px 0px 33px;
  }
}

.about__btn:hover {
  background-color: #FFBD30;
}

.about__btn:active,
.about__btn-text:active {
  color: rgba(119, 110, 110, 0.3);
}
