@import "../utils/utils.less";

.my-profile {
  position: relative;
  z-index: 9;
  min-width: 100%;
  width: 100%;
  background-color: #E5E5E5;

  @media (min-width: @tablet-width) {
    position: relative;
    z-index: 9;
    }

    @media (min-width: @desktop-width) {
    background-color: transparent;
    position: relative;
    z-index: 10;
    }
}

.my-profile__container {
  display: flex;
  flex-direction: column;
  min-width: 318px;
  width: 318px;
  min-height: 436px;
  justify-content: space-between;
  align-items: center;
  border: solid 1px transparent;
  margin-top: -15px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 38px;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    min-width: 766px;
    width: 766px;
    min-height: 500px;
    margin-top: -45px;
    margin-bottom: 0px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 45px;
    padding-bottom: 0px;
    padding-left: 0px;
    padding-right: 0px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 1fr 1fr;
    min-width: 1438px;
    width: 1438px;
    min-height: 448px;
    padding: 0px 0px 0px 0px;
    margin: -191px auto 0px auto;
  }
}

.my-profile__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 236px;

  @media (min-width: @tablet-width) {
    grid-column-start: 1;
    grid-column-end: 5;
    grid-row-start: 1;
    grid-row-end: 2;
    width: 624px;
    margin-top: 68px;
    margin-bottom: 0px;
    margin-left: 71px;
    margin-right: 0px;
  }

  @media (min-width: @desktop-width) {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 3;
    width: 330px;
    height: 190px;
    padding: 0px 0px 0px 0px;
    margin: -261px 0px 0px -5px;
  }
}

.my-profile__level {
  width: 100px;
  height: 100px;
  background-image: url("../img/my-profile-level.svg");
  background-repeat: no-repeat;
  background-position: center;

  @media (min-width: @tablet-width) {
    background-image: url("../img/my-profile-level.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 94px;
    margin-top: -7px;
    margin-bottom: 0px;
    margin-left: 0px;
    margin-right: 0px;
  }

  @media (min-width: @desktop-width) {
    background-image: url("../img/my-profile-level-desktop.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 94px;
    margin-top: -9px;
    margin-bottom: 0px;
    margin-left: 0px;
    margin-right: 0px;
  }
}

.my-profile__level-number {
  display: block;
  font-family: "Blogger Sans", Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 38px;
  line-height: 38px;
  text-align: center;
  color: #161C35;
  margin-top: 11px;
}

.my-profile__level-text {
  display: block;
  font-family: "Blogger Sans", Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 33px;
  text-align: center;
  color: #444444;
}

.my-profile__avatar {
  margin: 0px 0px 0px 0px;

  @media (min-width: @tablet-width) {
    margin-top: -11px;
    margin-bottom: 0px;
    margin-left: 0px;
    margin-right: 3px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: 0px;
    margin-right: 0px;
  }
}

.my-profile__icon {
  width: 100px;
  height: 100px;
  border-radius: 50px;
  margin: 0px 0px 0px 0px;

  @media (min-width: @tablet-width) {
    width: 94px;
    height: 94px;
    border-radius: 5px;
    margin: 0px 0px 0px 0px;
  }

  @media (min-width: @desktop-width) {
    width: 190px;
    height: 190px;
    border-radius: 0px;
    margin: 0px 0px 0px 0px;
  }
}

.my-profile__button {
  width: 270px;
  height: 40px;
  background-color: #DFE3F0;
  border: none;
  border-radius: 20px;
  margin-top: 16px;

  @media (min-width: @tablet-width) {
    grid-column-start: 1;
    grid-column-end: 5;
    grid-row-start: 2;
    grid-row-end: 3;
    width: 618px;
    height: 60px;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 18px;
    display: flex;
    align-items: center;
    letter-spacing: 0.09em;
    margin-top: -25px;
    margin-bottom: 0px;
    margin-left: auto;
    margin-right: auto;
    padding: 0px 0px 0px 228px;
    box-sizing: border-box;
  }

  @media (min-width: @desktop-width) {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 3;
    width: 190px;
    height: 47px;
    border-radius: 0%;
    margin-top: -22px;
    margin-bottom: 0px;
    margin-left: 136px;
    margin-right: 0px;
    padding: 0px 0px 0px 0px;
  }
}

.my-profile__button:hover {
  background-color: #D1D7E9;
}

.my-profile__button:active {
  background-color: #DFE3F0;
}

.my-profile__btn-text {

  font-family: "Blogger Sans", Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 12px;
  text-transform: uppercase;
  color: #161C35;

  @media (min-width: @tablet-width) {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 18px;
    letter-spacing: 0.09em;
  }

  @media (min-width: @desktop-width) {
    display: none;
  }
}

.my-profile__btn-text-desktop {
  display: none;

  @media (min-width: @tablet-width) {
    display: none;
  }

  @media (min-width: @desktop-width) {
    display: inline-block;
    font-family: "Blogger Sans", Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 18px;
    text-transform: uppercase;
    color: #161C35;
    margin: 0 auto;
  }
}

.my-profile__tags {
  display: block;
  width: 270px;
  min-height: 86px;
  border: solid 1px #444444;
  border-radius: 10px;
  padding: 26px 0px 0px 25px;
  margin-top: 32px;
  box-sizing: border-box;

  @media (min-width: @tablet-width) {
    grid-column-start: 1;
    grid-column-end: 5;
    grid-row-start: 3;
    grid-row-end: 4;
    width: 618px;
    min-height: 95px;
    margin-top: -95px;
    margin-bottom: 0px;
    margin-left: auto;
    margin-right: auto;
    padding: 31px 0px 0px 40px;
    box-sizing: border-box;
  }

  @media (min-width: @desktop-width) {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 3;
    width: 734px;
    height: 94px;
    margin-top: -52px;
    margin-bottom: 0px;
    margin-left: 122px;
    margin-right: 0px;
    padding: 35px 0px 0px 40px;
    box-sizing: border-box;
  }
}

.my-profile__tags:hover {
  color: #444444;
  opacity: 0.6;
}

.my-profile__tags:active {
  color: #444444;
