  @media (min-width: @tablet-width) {
    display: inline-block;
    width: 100%;
    height: 100%;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 22px;
    color: #444444;
    opacity: 0.6;
    margin: 0px 0px 0px 0px;
    padding: 0px 0px 0px 0px;
    border: none;
    background-color: transparent;
    resize: none;
  }

  @media (min-width: @desktop-width) {
    display: inline-block;
    width: 100%;
    height: 100%;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 22px;
    color: #444444;
    opacity: 0.6;
    margin: 0px 0px 0px 0px;
    padding: 0px 0px 0px 0px;
    border: none;
    background-color: transparent;
    resize: none;
  }
}

